
export class ContactView {

	constructor(){

		// init form values empty strings or if theyve already been 
		// cached in session storage using that value 
		this.name = window.sessionStorage.getItem("name") 
    	? document.getElementById("contact-page-form-name").value = 
    	window.sessionStorage.getItem("name")
    	: '';
    	//MOBILE
    	this.name_mobile = window.sessionStorage.getItem("name_mobile") 
    	? document.getElementById("contact-page-form-name_mobile").value = 
    	window.sessionStorage.getItem("name_mobile")
    	: '';

		this.email = window.sessionStorage.getItem("email") 
    	? document.getElementById("contact-page-form-email").value =
    	window.sessionStorage.getItem("email")
    	: '';
    	//MOBILE
    	this.email_mobile = window.sessionStorage.getItem("email_mobile") 
    	? document.getElementById("contact-page-form-email_mobile").value = 
    	window.sessionStorage.getItem("email_mobile")
    	: '';

		this.telephone = window.sessionStorage.getItem("telephone") 
    	? document.getElementById("contact-page-form-telephone-number").value = 
    	window.sessionStorage.getItem("telephone")
    	: '';
    	//MOBILE
    	this.telephone_mobile = window.sessionStorage.getItem("telephone_mobile") 
    	? document.getElementById("contact-page-form-telephone_mobile").value = 
    	window.sessionStorage.getItem("telephone_mobile")
    	: '';

		this.msg = window.sessionStorage.getItem("msg") 
    	? document.getElementById("contact-page-form-msg").value = 
    	window.sessionStorage.getItem("msg").trim()
    	: ''.trim();
    	//MOBILE
    	this.msg_mobile = window.sessionStorage.getItem("msg_mobile") 
    	? document.getElementById("contact-page-form-msg_mobile").value = 
    	window.sessionStorage.getItem("msg_mobile")
    	: '';

    	this.init = () => {
    		this.presave_input_data();
    		this.remove_session_storage_when_form_resets();
    		this.hide_response_message();
    		this.open_and_close_navbar();
    	}
	}

	open_and_close_navbar = () => {

		const navbar_btn_open = 
		document.getElementById("mobile-menu-btn")

		const navbar_btn_close = 
		document.getElementById("mobile-menu-close")

		const menu = document.getElementById("mobile-menu");

		navbar_btn_open.addEventListener("click", (event)=>{

			menu.style.display = 'block';
		})

		navbar_btn_close.addEventListener("click", (event)=>{
			menu.style.display = 'none';
		})
	}

	hide_response_message = () => {
		const div = 
		document.getElementById("contact-main-section1-form1-div1")
		div.style.display = 'none';
		//MOBILE
		const p = document.getElementById("contact-main-mobile-message")
		p.style.display = 'none';
	}

	// save live input data into view constructor and to session storage
	presave_input_data = () => {

		const name = document.getElementById("contact-page-form-name");
		const email = document.getElementById("contact-page-form-email");
		const telephone = document.getElementById("contact-page-form-telephone-number");
		const msg = document.getElementById("contact-page-form-msg");

		name.addEventListener("input", (event)=>{

			this.name = event.target.value.trim();
			window.sessionStorage.setItem("name", this.name);
		})

		email.addEventListener("input", (event)=>{

			this.email = event.target.value.trim();
			window.sessionStorage.setItem("email", this.email);
		})

		telephone.addEventListener("input", (event)=>{

			this.telephone = event.target.value.trim();
			window.sessionStorage.setItem("telephone", this.telephone);
		})

		msg.addEventListener("input", (event)=>{
			event.target.value.trim();
			this.msg = event.target.value.trim();
			window.sessionStorage.setItem("msg", this.msg);
		})
		// MOBILE
		const name_mobile = document.getElementById("contact-page-form-name_mobile");
		const email_mobile = document.getElementById("contact-page-form-email_mobile");
		const telephone_mobile= document.getElementById("contact-page-form-telephone-number_mobile");
		const msg_mobile = document.getElementById("contact-page-form-msg_mobile");

		name_mobile.addEventListener("input", (event)=>{

			this.name_mobile = event.target.value.trim();
			window.sessionStorage.setItem("name_mobile", this.name_mobile);
		})

		email_mobile.addEventListener("input", (event)=>{

			this.email_mobile = event.target.value.trim();
			window.sessionStorage.setItem("email_mobile", this.email_mobile);
		})

		telephone_mobile.addEventListener("input", (event)=>{

			this.telephone_mobile = event.target.value.trim();
			window.sessionStorage.setItem("telephone_mobile", this.telephone_mobile);
		})

		msg_mobile.addEventListener("input", (event)=>{
			event.target.value.trim();
			this.msg_mobile = event.target.value.trim();
			window.sessionStorage.setItem("msg_mobile", this.msg_mobile);
		})

	}

	remove_session_storage_when_form_resets = () => {

		const reset_btn = 
		document.getElementById("contact-main-section1-form1-div-div2-input1");

		reset_btn.addEventListener("click", (event)=>{
			window.sessionStorage.removeItem("name");
			window.sessionStorage.removeItem("email");
			window.sessionStorage.removeItem("telephone");
			window.sessionStorage.removeItem("msg");

		})

		//MOBILE

		const reset_btn_mobile = 
		document.getElementById("contact-main-mobile-buttons_reset");

		reset_btn.addEventListener("click", (event)=>{
			window.sessionStorage.removeItem("name_mobile");
			window.sessionStorage.removeItem("email_mobile");
			window.sessionStorage.removeItem("telephone_mobile");
			window.sessionStorage.removeItem("msg_mobile");
		})

	}

	submitForm = (modelHandlerFunction) => {

		const form = 
		document.getElementById("contact-main-section1-form1");

		form.addEventListener("submit",(event)=>{

			event.preventDefault();

			this.name = document.getElementById("contact-page-form-name").value.trim();
			this.email = document.getElementById("contact-page-form-email").value.trim();
			this.telephone = document.getElementById("contact-page-form-telephone-number").value.trim();
			this.msg = document.getElementById("contact-page-form-msg").value.trim();

			if(
				this.name.length > 0 &&
				this.email.length > 0 &&
				this.telephone.length > 0 &&
				this.msg.length > 0 
			){
				if(
					this.email.includes("@") &&
					this.email.length > 5
				){

					modelHandlerFunction({
						name: this.name,
						email: this.email,
			            telephone: this.telephone,
						msg: this.msg
					})
					.then((res)=>{
						if(res.status){

							const div = 
							document.getElementById("contact-main-section1-form1-div1")
							const p = 
							document.getElementById("contact-main-section1-form1-div1-p1")

							div.style.display = 'flex';

							p.classList.remove("contact-main-section1-form1-div1-p1-fail")

							p.classList.add("contact-main-section1-form1-div1-p1-success")

							p.textContent = "Message sent successfully"

							document.getElementById("contact-page-form-name").value = "";
							this.name = "";
							window.sessionStorage.removeItem("name");

							document.getElementById("contact-page-form-email").value = "";
							this.email = "";
							window.sessionStorage.removeItem("email");


							document.getElementById("contact-page-form-telephone-number").value = "";
							this.telephone = "";
							window.sessionStorage.removeItem("telephone");

							document.getElementById("contact-page-form-msg").value = "";
							this.msg = ""
							window.sessionStorage.removeItem("msg");
						}
						else{
							const div = 
							document.getElementById("contact-main-section1-form1-div1")
							const p = 
							document.getElementById("contact-main-section1-form1-div1-p1")

							div.style.display = 'flex';

							p.classList.add("contact-main-section1-form1-div1-p1-error")

							p.textContent = "Error sending message"
						}
					})
					.catch((err)=>{
						console.log(err)

						const div = 
							document.getElementById("contact-main-section1-form1-div1")
							const p = 
							document.getElementById("contact-main-section1-form1-div1-p1")

							div.style.display = 'flex';

							p.classList.remove("contact-main-section1-form1-div1-p1-success")

							p.classList.add("contact-main-section1-form1-div1-p1-fail")

							p.textContent = "Error sending message"
					})

				}
				else{
					const div = 
					document.getElementById("contact-main-section1-form1-div1")
					const p = 
					document.getElementById("contact-main-section1-form1-div1-p1")

					div.style.display = 'flex';

					p.classList.remove("contact-main-section1-form1-div1-p1-success")

					p.classList.add("contact-main-section1-form1-div1-p1-fail")

					p.textContent = "Please Enter a valid Email"

				}
			}
			else{

			const div = 
			document.getElementById("contact-main-section1-form1-div1")
			const p = 
			document.getElementById("contact-main-section1-form1-div1-p1")

			div.style.display = 'flex';

			p.classList.remove("contact-main-section1-form1-div1-p1-success")

			p.classList.add("contact-main-section1-form1-div1-p1-fail")

			p.textContent = "Please Fill out All Fields"

			}
		})

		// MOBILE
		const form_mobile = 
		document.getElementById("contact-main-mobile-form");

		form_mobile.addEventListener("submit",(event)=>{

			event.preventDefault();

			this.name_mobile = document.getElementById("contact-page-form-name_mobile").value.trim();
			this.email_mobile = document.getElementById("contact-page-form-email_mobile").value.trim();
			this.telephone_mobile = document.getElementById("contact-page-form-telephone-number_mobile").value.trim();
			this.msg_mobile = document.getElementById("contact-page-form-msg_mobile").value.trim();

			if(
				this.name_mobile.length > 0 &&
				this.email_mobile.length > 0 &&
				this.telephone_mobile.length > 0 &&
				this.msg_mobile.length > 0 
			){
				if(
					this.email_mobile.includes("@") &&
					this.email_mobile.length > 5
				){

					modelHandlerFunction({
						name: this.name_mobile,
						email: this.email_mobile,
			            telephone: this.telephone_mobile,
						msg: this.msg_mobile
					})
					.then((res)=>{
						if(res.status){

							const p = 
							document.getElementById("contact-main-mobile-message")

							p.style.display = 'flex';

							p.classList.remove("main-mobile-message-error")

							p.classList.add("main-mobile-message-success")

							p.textContent = "Message sent successfully"

							document.getElementById("contact-page-form-name_mobile").value = "";
							this.name_mobile = "";
							window.sessionStorage.removeItem("name_mobile");

							document.getElementById("contact-page-form-email_mobile").value = "";
							this.email_mobile = "";
							window.sessionStorage.removeItem("email_mobile");


							document.getElementById("contact-page-form-telephone-number_mobile").value = "";
							this.telephone_mobile = "";
							window.sessionStorage.removeItem("telephone_mobile");

							document.getElementById("contact-page-form-msg_mobile").value = "";
							this.msg_mobile = ""
							window.sessionStorage.removeItem("msg_mobile");
						}
						else{

							const p = 
							document.getElementById("contact-main-mobile-message")

							p.style.display = 'flex';

							p.classList.remove("main-mobile-message-success")

							p.classList.add("main-mobile-message-error")

							p.textContent = "Error sending message"
						}
					})
					.catch((err)=>{
					
						const p = 
						document.getElementById("contact-main-mobile-message")

						p.style.display = 'flex';

						p.classList.remove("main-mobile-message-success")

						p.classList.add("main-mobile-message-error")

						p.textContent = "Error sending message"
					})

				}
				else{
					const p = 
					document.getElementById("contact-main-mobile-message")

					p.style.display = 'flex';

					p.classList.remove("main-mobile-message-success")

					p.classList.add("main-mobile-message-error");

					p.textContent = "Please Enter a valid Email";

				}
			}
			else{

				const p = 
				document.getElementById("contact-main-mobile-message")

				p.style.display = 'flex';

				p.classList.remove("main-mobile-message-success")

				p.classList.add("main-mobile-message-error")

				p.textContent = "Please Fill out All Fields"

			}
		})
	}

 }